import React, { Component } from 'react';
import socketIOClient from "socket.io-client";
import md5 from 'md5';
import '../App.css';
import {WEBRTC_SERVER, SIGNALING_SERVER_PRO, SIGNALING_SERVER_DEV} from '../shared/constants'
import Axios from 'axios';

let SESSION_STATUS = window.Flashphoner.constants.SESSION_STATUS;
let STREAM_STATUS = window.Flashphoner.constants.STREAM_STATUS;
let socket;
let session;

export default class Home extends Component{
  constructor(props) {
    super(props);

    this.state = {
      publishing : false,
      minutes: 0,
      seconds: 0,
      millis: 0,
      running: false,
      remaining : 30,
      modalVisibleTimer : false
    }

    this._handleStartClick = this._handleStartClick.bind(this);
    this._handleStopClick = this._handleStopClick.bind(this);
    this._handleResetClick = this._handleResetClick.bind(this);
    this.stop = this.stop.bind(this);
    
    let signaling  = window.location.host === "911.video" ?  SIGNALING_SERVER_PRO : SIGNALING_SERVER_DEV;
    socket = socketIOClient(signaling);
    window.document.addEventListener('touchmove', function(e) { e.preventDefault(); }, { passive:false });
  
  }

  async componentDidMount(){
    let _self  =  this;

    if(localStorage.getItem('user')){
          const user = JSON.parse(localStorage.getItem('user'));
          this.setState({user : user});
      }else{
        this.props.history.goBack();
      }

      try {
          window.Flashphoner.init({flashMediaProviderSwfLocation: '../../public/assets/media-provider.swf'});
      } catch (e) {
          alert("Your browser doesn't support Flash or WebRTC technology needed for this example");
          return;
      }

      await navigator.geolocation.getCurrentPosition((position)=>{
        _self.setState({ coordinates : position});
      }, this.locationError);
  }

   locationError = (error)=>{
    switch(error.code) {
        case error.PERMISSION_DENIED:
            return ("User denied the request for Geolocation.")
            break;
        case error.POSITION_UNAVAILABLE:
            return alert("Location information is unavailable.")
            break;
        case error.TIMEOUT:
            return alert("The request to get user location timed out.")
            break;
        case error.UNKNOWN_ERROR:
            return alert("An unknown error occurred.")
            break;
    }
}

  _handleStartClick(event) {
    if (!this.state.running) {
        this.interval = setInterval(() => {
            this.tick();
        }, 100)

        this.setState({running: true})
    }
}

_handleStopClick(event) {        
    if (this.state.running) {
        clearInterval(this.interval);
        this.setState({running: false})
    }
}

startTimerOnModal = () => {
    
  clearInterval(this.intervalTimer);
  this.intervalTimer = window.setInterval(()=>{
    if(this.state.remaining === 0){

        if(this.state.modalVisibleTimer){
          this.stopRecording();
          window.clearInterval(this.intervalTimer);
          return ;
        }

      this.keepRecording();
      return;

    }

    this.setState({ remaining: (this.state.remaining - 1) });
  
  }, 1000); 


};

stopRecording = ()=>{
  this.setState({ modalVisibleTimer : false});
  clearInterval(this.intervalTimer);
  this.setState({ remaining : 30});
}

saveVideo = (obj)=>{
  return new Promise(async (resolve, reject)=>{
    try{
      let savedVideo = await Axios.post('https://app.eyenation.org/api-video/save-video', obj, {headers: {
        "cache-control": "no-cache",
        "content-type": "application/json",
      }});

      resolve(savedVideo);
    }catch(e){
        reject(e);
    }
  });

}

keepRecording = ()=>{
  this.setState({modalVisibleTimer : false})
  this.setState({ remaining : 30});
  clearInterval(this.intervalTimer);
}

_handleResetClick(event) {
    this._handleStopClick();
    this.update(0, 0, 0);
}

tick() {
    let millis = this.state.millis + 1;
    let seconds = this.state.seconds;
    let minutes = this.state.minutes;

    if (millis === 10) {
        millis = 0;
        seconds = seconds + 1;
    }

    if (seconds === 60) {
        millis = 0;
        seconds = 0;
        minutes = minutes + 1;
    }

    this.update(millis, seconds, minutes);
}

zeroPad(value) {
    return value < 10 ? `0${value}` : value;
}

update(millis, seconds, minutes) {
    this.setState({
        millis: millis,
        seconds: seconds,
        minutes: minutes
    });
}

  loginByPhone =($event)=>{
      $event.preventDefault();
      this.props.history.push('/home');
  }

  getConstraints = ()=>{
    let constraints = {
      audio : true,
      video : true
    }

    constraints.video = {
      width: { min: 1080, max: 1080 },
      height: { min: 720, max: 720 },
      facingMode: { exact: "environment" }
    }

    return constraints;
  }

  live = async ()=>{
    let _self = this;
    this.setState({publishing : true, loading : true});
    this._handleStartClick();

    if(this.time_out){
      clearTimeout(this.time_out);
    }
    navigator.mediaDevices.getUserMedia(_self.getConstraints()).then(function(mediaStream) {
        try{
          let id =  md5(WEBRTC_SERVER + new Date());
          let stname = id;

          if(window.Flashphoner.getSessions().length > 0){
              console.log("have session");
              let session = window.Flashphoner.getSessions()[0];

              if (window.Browser.isSafari()) {
                window.Flashphoner.playFirstVideo(document.getElementById("local-video"), true);
              }

              return session.createStream({
                name: stname,
                display: document.getElementById("local-video"),
                record: true,
                receiveVideo: true,
                receiveAudio: true,
                constraints:_self.getConstraints()
              }).on(STREAM_STATUS.PUBLISHING,(stream)=>{
                console.log(STREAM_STATUS.PUBLISHING);
                _self.setState({stream : stream});
                _self.setState({ started : new Date().getTime()});
                _self.setState({ publishing : true, loading:false});
                
                _self.time_out = window.setTimeout(()=>{
                  _self.showModalTimer();
                  _self.startTimerOnModal();
                }, 5000);

              }).on(STREAM_STATUS.UNPUBLISHED, (stream) =>{

              }).on(STREAM_STATUS.FAILED,(stream)=>{

              }).publish();
          }else{
            window.Flashphoner.createSession({ urlServer:WEBRTC_SERVER}).on(SESSION_STATUS.ESTABLISHED, function (session) {
              try{
  
                  if (window.Browser.isSafari()) {
                      window.Flashphoner.playFirstVideo(document.getElementById("local-video"), true);
                  }
  
                  session = session.createStream({
                    name: stname,
                    display: document.getElementById("local-video"),
                    record: true,
                    receiveVideo: true,
                    receiveAudio: true,
                    constraints:_self.getConstraints()
                  }).on(STREAM_STATUS.PUBLISHING,(stream)=>{
                    console.log(STREAM_STATUS.PUBLISHING);
                    _self.setState({stream : stream});
                    _self.setState({ started : new Date().getTime()});
                    _self.setState({ publishing : true, loading:false});
                    
                    _self.time_out = window.setTimeout(()=>{
                      _self.showModalTimer();
                      _self.startTimerOnModal();
                    }, 5000);
  
                  }).on(STREAM_STATUS.UNPUBLISHED, (stream) =>{
  
                  }).on(STREAM_STATUS.FAILED,(stream)=>{
  
                  }).publish();
  
              }catch(e){alert(e.message)}
          }).on(SESSION_STATUS.CONNECTED, function (session) {
            _self.setState({ publishing : true, loading:false});
            _self.setState({session : session}); 
  
            let data = {
                type : "stream::started",
                user: "hello@fastcodelab.com",
                username: "fastcode",
                stname: stname,
                token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VyIjoiY3Zhc3F1ZXpkZXZAZ21haWwuY29tIiwiaWF0IjoxNTY0Njc3MTAwfQ.nwJfsLJhrFPPGNhWxuxcJybaI5Uwt6axSKbL-877smw",
                ws_url: (WEBRTC_SERVER + stname),
                phone: _self.state.user.phone,
                device: "webapp",
                is911: _self.state.is911,
                security_partner: "EyeNationTestSchool",
                coordinates: {lat:_self.state.coordinates ?  _self.state.coordinates.coords.latitude : "0.0", lng: _self.state.coordinates ? _self.state.coordinates.coords.longitude :  "0.0"} 
              }
  
              _self.setState({live : data});
              socket.emit("message", data);
  
          }).on(SESSION_STATUS.DISCONNECTED, function (session) {
              let ended  = _self.state.live;
              ended.type = "stream::end";
              //ended.recorded_video =  this.state.stream.getRecordInfo();
              _self.setState({ live : ended});
              socket.emit("message", ended);
          });
          }
      }catch(e){
        alert("flashphoner err " + e.message);
      }   
    }).catch(function(err) {
      alert(err.message);
    });
   };

   
  showModalTimer = ()=>{
    if(this.state.publishing){
      this.setState({modalVisibleTimer : true});
    }
  }


  showModalSaved = ()=>{
    this.setState({ modalSaved : true});
  }

  _renderModalSaved = ()=>{
    if(this.state.modalSaved){
      return (<div className="modal-saved">
          <div className="modal-content-saved">
            <div className="modal-saved-title">
              <p>YOUR VIDEO WAS SAVED</p>
            </div>
            <div className="actions">
              <a className="button is-primary" onClick={()=>this.setState({modalSaved : false})}>CAPTURE AGAIN</a>
            </div>
          </div>
        </div>);
    }else{
      return null;
    }
  }

 stop = async ()=>{
    this.setState({publishing : false, loading :false});
    this._handleStopClick();
    this._handleResetClick();
    if(this.state.modalVisibleTimer){
      this.setState({ modalVisibleTimer : false});
       clearInterval(this.intervalTimer);
      this.setState({ remaining : 30});
    }

    try{
      this.state.stream.stop();
      let streanEnded = new Date().getTime();
      let ended  = this.state.live;
      ended.type = "stream::end";
      ended.recorded_video =  ("http://streaming.911.video:9091/client/records/" +  this.state.stream.getRecordInfo());
      this.setState({ live : ended, ended : new Date().getTime()});
      socket.emit("message", ended);
  
      let videoObj = {
        "saveid": ended.stname,
        "user":ended.user,
        "type": "stream::save",
        "tags": '',
        "category": '',
        "recorded_video": ended.recorded_video,
        "description":'',
        "name" :'',
        "stream_started": this.state.started,
        "stream_ended": streanEnded,
        "is911": ended.is911,
        "isHelp":  ended.isHelp || false,
        "coordinates": JSON.stringify({
         "lat" : ended.coordinates.lat,
         "lng": ended.coordinates.lng
        }),
        "token": ended.token,
        "stname": ended.stname,
        "validated_password": false,
        "sendToAuction": false,
        "device" : "webapp"
      }
  
      console.log("obj to save", videoObj);
  
      let video = await this.saveVideo(videoObj);
      
      if(video){
        this.showModalSaved();
      }
    }catch(e){
      alert(e.message);
    }
  }

  _renderRougeBtn = ()=>{
    if(!this.state.publishing){
        return (<div onClick={this.live} className="rougeButton"></div>);
    }

    return (null);
  }
    
  _renderStopBtn = ()=>{
    if(this.state.publishing){
      return (<div onClick={this.stop} className="stopButton"></div>);
    }

    return (null);
  }

  _renderLoader = ()=>{
    if(this.state.loading){
      return (<div className="loader"></div>);
    }

    return (null);
  }

  _render911Btn =()=>{
    if(!this.state.publishing){
      return (<div onClick={()=>{
        if(window.confirm('Are you sure want call 911 ?')){
          this.setState({is911 : '911'});
          this.live(); 
          this.callElement.click();
        }else{
          this.live(); 
        }
       }} className="btn911"></div>);
    }
    
    return (null);
  }

  openMenu = ()=>{
    this.setState({openModalMenu : true});
  }

  closeMenu= ()=>{
    this.setState({openModalMenu : false});
  }

  _renderModalMenu = ()=>{
    if(this.state.openModalMenu){
      return (<div className="modal-menu">
                <div className="menu-username">
                  <ul className='menu-items'>
                    <li className='menu-username-item' style={{marginTop: '20px'}}><img onClick={this.closeMenu} className='close-img' src={'/assets/images/ic_close_menu.png'} alt={'close'} />Javier Gomez</li>
                    <li><a className='about-link' href={'https://eyenation.org'} target={'_blank'}><img className='about-img' src={'/assets/images/ic_about.png'} alt={'About us'} />About us</a></li>
                  </ul>
                </div>
              </div>
      );
    }

    return null;
  }

  _renderModalTimer = ()=>{
    if(this.state.modalVisibleTimer){
      return (
        <div className="modal-timer">
          <div className="modal-content-timer">
            <div className="modal-timer-title">
               <p>DO YOU WANT TO CONTINUE VIDEO CAPTURE</p>
            </div>
            <div className="contents-timer">
              <p>{this.state.remaining} seg</p> 
            </div>
            <div className="actions">
              <a className="button is-primary" onClick={this.keepRecording}>YES</a>
              <a className="button is-primary" onClick={this.stop}>NO</a>
            </div>
          </div>
        </div>
      );
    }

    return null;
  }

  _renderMenu = ()=>{
    return (<div onClick={this.openMenu} className="menu"><img alt={'menu'} src={'/assets/images/menu.png'}/></div>);
  }

  _renderLiveLabel = ()=>{
    if(this.state.publishing){
        return (<div className="liveLabel"><p>LIVE</p></div>);
    }

    return (null);
  }

  _renderLocation = ()=>{
    if(this.state.coordinates && this.state.publishing){
      return (
        <div className={"coordinates"} style={{display : 'flex'}}>
        <p className='coordinates-text'><img className="location-image"  src={'/assets/images/ic_geo.png'} alt={'Coordinates'} />{`lat : ${this.state.coordinates.coords.latitude} lng : ${this.state.coordinates.coords.longitude}`}</p>
      </div>
      )
    }else{
      return null;
    }
  }

  _renderTimer = ()=>{
    if(this.state.publishing){
      return (
        <div className="timer">
            <div className="red-indicator"></div>
            <p>{this.zeroPad(this.state.minutes)}:{this.zeroPad(this.state.seconds)}:0{this.state.millis}</p>
        </div>
        );
    }
  }

    render(){
        return (
            <div id='main' className="main">
                {this._renderModalSaved()}
                {this._renderModalMenu()}
                {this._renderModalTimer()}
                {this._renderLoader()}
                <div id={'local-video'}></div>
                {this._renderMenu()}
                {this._renderLiveLabel()}
                {this._renderTimer()}
                <div className="recordButton">
                  {this._renderRougeBtn()}
                  {this._renderStopBtn()}
                  {this._render911Btn()}
                </div>
                {this._renderLocation()}
                <div className="logo">
                  <img src={'/assets/images/eyenation.png'} alt={'Eyenation.org'} />
                </div>
                <a ref={input => this.callElement = input} href={"tel:3012904420"} style={{display:"none"}}>{''}</a>
            </div>
        );
    }
}


